import React from "react"
import Footer from "./footer/footer"
import { HashRouter as Router, Route, Link } from "react-router-dom"
import Startpage from "./start-page/start-page"
import Radioplayer from "./radioplayer/radioplayer"
import Weather from "./weather/weather"
import Sprinttwo from "./sprint-two/sprint-two"
import Sprintthree from "./sprint-three/sprint-three"




class App extends React.Component {
  render() {
    return (
      <Router>
        <div>
         <Route path="/" exact component={Startpage} />
         <Route path="/radioplayer" exact component={Radioplayer} />
         <Route path="/weather" exact component={Weather} />
         <Route path="/sprinttwo" exact component={Sprinttwo} />
         <Route path="/sprintthree" exact component={Sprintthree} />
         <Footer />
        </div>
      </Router>
    )
  }
}

export default App
